body {
  margin: 0;
  padding: 0;
  background-image: url('https://d3njjcbhbojbot.cloudfront.net/api/utilities/v1/imageproxy/https://images.ctfassets.net/wp1lcwdav1p1/1e2jX0KHeIE8lyzvUIjny5/a6626a4d2736f62cebcf6886f02b3a8c/business-degree_1500x680.JPG?w=1500&h=680&q=60&fit=fill&f=faces&fm=jpg&fl=progressive&auto=format%2Ccompress&dpr=1&w=1000');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: Arial, sans-serif;
}

/* Form Container */
.formContainer {
  margin: 10px;
  padding: 30px;
  background-color: #ffffff;
  border: 1px solid #e2e2e2;
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
}

.formContainer:hover {
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
}

/* Form Header */
h2 {
  color: #2c49ed;
  font-size: 28px;
  font-weight: 700;
}

/* Input Fields */
.formInputs {
  margin-bottom: 20px;
}

.formInputs input {
  width: 100%;
  height: 45px;
  padding: 10px 15px;
  border-radius: 5px;
  border: 1px solid #ced4da;
  font-size: 16px;
  background-color: #f9f9f9;
  transition: all 0.3s ease;
}

.formInputs input:focus {
  border-color: #2c49ed;
  background-color: #fff;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  outline: none;
}

/* Select and Radio Inputs */
.formInputs select,
.formInputs .gender {
  width: 100%;
  height: 45px;
  padding: 10px 15px;
  border-radius: 5px;
  border: 1px solid #ced4da;
  font-size: 16px;
  background-color: #f9f9f9;
  transition: all 0.3s ease;
}

.formInputs select:focus,
.formInputs .gender:focus {
  border-color: #2c49ed;
  background-color: #fff;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  outline: none;
}

/* Container for Side-by-Side Inputs */
.formInputs__side {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  gap: 20px;
}

.formInputs__side__inner {
  flex: 1;
}

.formInputs__side__inner select {
  width: 100%;
  height: 45px;
  padding: 10px 15px;
  border-radius: 5px;
  border: 1px solid #ced4da;
  font-size: 16px;
  background-color: #f9f9f9;
  transition: all 0.3s ease;
  appearance: none;
  background-image: url('data:image/svg+xml;base64,<your-down-arrow-svg>');
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 12px;
}

.formInputs__side__inner select:focus {
  border-color: #2c49ed;
  background-color: #fff;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  outline: none;
}

/* Label Styling */
.formInputs__side__inner label {
  margin-bottom: 5px;
  font-weight: 600;
  font-size: 16px;
  color: #333;
  display: block;
}

/* Gender Input */
.gender {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.gender div {
  width: 48%;
}

.gender label {
  margin-left: 5px;
  font-weight: 500;
}

/* Buttons */
button[type="submit"] {
  height: 50px;
  border-radius: 5px;
  font-size: 18px;
  font-weight: 600;
  background-color: #2c49ed;
  border: none;
  color: #ffffff;
  transition: all 0.3s ease;
}

button[type="submit"]:hover {
  background-color: #1d37c2;
  transform: translateY(-2px);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
}

button[type="submit"]:active {
  transform: translateY(2px);
  box-shadow: none;
}

button[type="button"] {
  background-color: transparent;
  color: #2c49ed;
  font-size: 16px;
  padding: 10px;
  transition: all 0.3s ease;
}

button[type="button"]:hover {
  text-decoration: underline;
  color: #1d37c2;
}

/* Navigation Tabs */
.nav-tabs .nav-link {
  color: #2c49ed;
  font-weight: 600;
  border-radius: 5px 5px 0 0;
  background-color: #f9f9f9;
  border: none;
  padding: 10px 20px;
  transition: all 0.3s ease;
}

.nav-tabs .nav-link.active {
  background-color: #2c49ed;
  color: white;
  border-bottom: 3px solid #2c49ed;
}

.nav-tabs .nav-link:hover {
  background-color: #1d37c2;
  color: #ffffff;
}

/* Media Queries */

/* Small Devices (tablets, 768px and down) */
@media screen and (max-width: 768px) {
  .formContainer {
    padding: 20px;
  }

  .formInputs input,
  .formInputs select,
  .formInputs .gender {
    height: 40px;
    font-size: 14px;
  }

  .formInputs__side {
    flex-direction: column;
  }

  .formInputs__side__inner {
    width: 100%;
    margin-bottom: 15px;
  }

  button[type="submit"] {
    height: 45px;
    font-size: 16px;
  }
}

/* Extra Small Devices (phones, 480px and down) */
@media screen and (max-width: 480px) {
  h2 {
    font-size: 24px;
  }

  .formInputs input,
  .formInputs select,
  .formInputs .gender {
    height: 35px;
    font-size: 14px;
  }

  .formInputs__side {
    flex-direction: column;
  }

  .formInputs__side__inner {
    width: 100%;
    margin-bottom: 15px;
  }

  button[type="submit"] {
    height: 40px;
    font-size: 14px;
  }
}

/* Tiny Devices (phones, 320px and down) */
@media screen and (max-width: 320px) {
  h2 {
    font-size: 20px;
  }

  .formContainer {
    padding: 10px;
  }

  .formInputs input,
  .formInputs select,
  .formInputs .gender {
    height: 30px;
    font-size: 12px;
  }

  button[type="submit"] {
    height: 35px;
    font-size: 12px;
  }
}



/* Home */

.headerBrand {
  font-size: 1.5rem;
}

.searchIcon {
  font-size: 1.75rem;
  margin-right: 6px;
  color: #01af6a;
}

.headerTitle {
  font-size: 2rem;
  margin-left: 10px;
  color: #01af6a;
}

.headerSubtitle {
  color: #333;
  text-align: center;
}
